.nav
    display: block
    margin-bottom: 20px

    &:last-child
        margin-bottom: 0

.human
    background: rgba(255,255,255,0.1)
    padding: 10px

    border-radius: 30px
    transition: 0.3s

    cursor: pointer

    display: flex
    align-items: center

    &:hover
        background: #fff

        span
            color: #000
            font-weight: 500

.name
    display: block
    margin-left: 10px

.active .human
    background: #fff
    color: #18181D
    font-weight: 500

@media (max-width: 950px)
    .name
        font-size: 12px

@media (max-width: 700px)
    .human
        &:last-child
            margin-bottom: auto

    .name
        margin-right: 10px
